@import url(https://fonts.googleapis.com/css?family=Heebo:100,300,400,500,700&subset=hebrew);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Hee'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body, #root {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.news-page {
	width: 100%;
	height: 100%;
}
.news-runner {
	background-color: white;
	width: 100vw;
	height: 85px;
}
.center-container {
	margin: auto;
	position: absolute;
	top: 38%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
.desc {
	font-family: Heebo, sans-serif;
	color: white;
	margin-top: 15px;
	margin-bottom: 15px;
	direction: rtl;
	font-size: 20px;
}
.speed-settings {
	position: absolute;
	bottom: 0;
	color: white;
	font-family: Heebo, sans-serif;
	font-size: 20px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	direction: rtl;
	padding-bottom: 20px;
}
.news-runner-container {
	overflow: hidden;
	height: 85px;
}
.speed-settings > span {
	padding-right: 7px;
	padding-left: 7px;
	cursor: pointer;
	opacity: 0.25;
}
.category-btn {
	width: 146px;
	font-family: Heebo, sans-serif;
	font-size: 24px;
	font-weight: 900;
	font-stretch: normal;
	font-style: normal;
	line-height: normal;
	letter-spacing: normal;
	color: #000c2d;
	cursor: pointer;
	opacity: 0.5;
}
.category-btn.selected {
	opacity: 1;
}
.speed-settings > span.selected {
	opacity: 1;
}
.categories-container {
	display: flex;
	align-items: center;
	height: 85px;
}
.runner-wrapper {
	box-sizing: content-box;
	padding-bottom: 15px;
	overflow-x: scroll;
	overflow-y: hidden;
	height: inherit;
	width: inherit;
	white-space: nowrap;
	direction: rtl;
}
.footer {
	position: absolute;
	bottom: 0;
	height: 100px;
	width: 100%;
}
.footer-relative {
	position: relative;
	width: 100%;
	height: 100%;
}
.settings-button {
	position: absolute;
	bottom: 0;
	right: 0;
	color: white;
	font-family: Heebo, sans-serif;
	padding-right: 20px;
	padding-bottom: 20px;
	cursor: pointer;
}
.news-item {
	height: 80px;
	cursor: pointer;
	padding-top: 27px;
	display: inline-block;
	padding-left: 20px;
	padding-right: 20px;
	font-family: Heebo, sans-serif;
	font-size: 20px;
	font-weight: bold;
	font-stretch: normal;
	font-style: normal;
	line-height: normal;
	letter-spacing: normal;
	text-align: right;
	color: #000000;
}
.news-item:hover {
	text-decoration: underline;
}

.App {
  text-align: center;
  background-color: #000c2d;
  width: 100%;
  height: 100%;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

